.main-div{position:absolute;inset:0;transition:all .5ms;z-index:10}.main-div::after{content:"";visibility:hidden;position:absolute;inset:0;backdrop-filter:blur(2px);z-index:10}.main-div.animating{animation:shrink 500ms ease-in-out forwards}.main-div.animating:after{border-radius:0 35px 35px 0;visibility:visible}.main-div.animating .exit-btn{animation:scaler 500ms ease-in-out;animation-fill-mode:forwards}.main-div.maximize{animation:maximize 100ms;animation-fill-mode:forwards}.exit-btn{position:absolute;border-radius:50%;padding:15px;right:-2%;top:45%;color:#e9c7c7;transform:scale(0);z-index:1000;background-color:#c97839;border:none;transition:all 200ms;cursor:pointer}.exit-btn img{width:50px}.btton{z-index:100000;position:fixed;display:flex;cursor:pointer;align-items:center;font-weight:bold;font-size:larger;gap:15px;right:1%;color:#fff;top:5%}.btton img{height:40px;width:40px}.host-data{position:absolute;top:40%;left:5%;animation:scaler 500ms ease-in-out;animation-fill-mode:forwards;text-transform:capitalize}.host-data .name-div{font-size:14px;font-style:italic;color:#e9c7c7;padding:14px 0;font-weight:bold}.host-data .desc-div{color:#fff}@keyframes shrink{0%{bottom:0%;top:0%;left:0%;right:0%;visibility:visible;border-radius:0}100%{bottom:9%;top:9%;left:-10%;right:30%;border-radius:0 50px 35px 0}}@keyframes maximize{0%{bottom:9%;top:9%;left:0%;right:8%;border-radius:0 35px 35px 0}100%{bottom:0%;top:0%;left:0%;right:0%;border-radius:0}}@keyframes scaler{0%{transform:scale(0)}50%{transform:scale(2)}100%{transform:scale(1)}}*{box-sizing:border-box;margin:0;font-family:sans-serif}body{overflow-x:hidden}.swiper{position:absolute !important;inset:0;transition:all .5s}.swiper:after{content:"";visibility:hidden;position:absolute;inset:0;backdrop-filter:blur(2px)}.animating .swiper{border-radius:0 35px 35px 0}.swiper-slide{background-color:gray}.home{background-image:url(./static/wolf.jpg);background-size:cover;background-repeat:no-repeat;background-position:center}.sidebar-container{position:fixed;top:0;display:flex;right:15%;height:100vh;background-color:#fff;z-index:9;justify-content:center;align-items:center;padding:15px;width:20vh}.sidebar-container ul{font-size:30px}.sidebar-container ul li{position:relative;margin:50px 0;width:fit-content}.sidebar-container ul li a{text-decoration:none;color:#008b8b;transition:all .5s}.sidebar-container ul li a:after{content:"";position:absolute;left:0%;min-width:none;width:0%;max-width:100%;top:100%;bottom:-10%;border-radius:35px;background-color:#02c9c9}.sidebar-container ul li a:hover{color:#02c9c9}.sidebar-container ul li a:hover:after{animation:liner 500ms ease-in-out;animation-fill-mode:forwards}@keyframes liner{from{width:0%}to{width:100%}}